@import url(https://fonts.googleapis.com/css2?family=Handlee&family=Lora:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  min-height: 100vh;
  position: relative;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

body {
  margin: 0;
  padding: 1em;
  font-family: "Handlee", cursive;
  color: #504f50;
  margin-bottom: 2em;
}

header,
footer {
  font-family: 'Lora', serif;
  left: 0;
  width: 100%;
}

header {
  font-size: calc(10px + 2vmin);
  top: 0;
  margin-bottom: 0.5em;
  position: static;
  position: initial;
  display: flex;
}

h1 {
  background: #000;
  padding: 0 0.25em;
  border-radius: 0.15em;
}

footer {
  position: absolute;
  font-size: calc(6px + 1vmin);
  bottom: 0;
}

.container {
  width: calc(35vw + 55vmin);
}

.no-tasks {
  height: 55vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.no-tasks-icons {
  display: grid;
}

img.check-icon {
  width: 4em;
  background: #d9dde0;
  border-radius: 5em;
  padding: calc(10% + 1vmax);
  margin: 1em auto;
}

.open-btn {
  float: right;
  position: absolute;
  top: 2em;
  right: 1em;
}

.btn {
  background-repeat: no-repeat;
  background-position: center;
  background-size: 1.5em;
}

.plus-btn {
  background-image: url(/demo-to-do-list/static/media/plus.87da0188.svg);
}

.check-btn:hover,
.checked {
  background-image: url(/demo-to-do-list/static/media/check.df4443c1.svg);
}

.checked {
  box-shadow: none !important;
}

.line-through {
  text-decoration: line-through;
  color: #8b9498;
}

.disable {
  color: #8b9498 !important;
  cursor: default !important;
  box-shadow: none !important;
}

.buttons {
  display: inline-flex;
}

.edit-btn {
  background-image: url(/demo-to-do-list/static/media/edit.e8f9ae73.svg);
  width: 3em;
}

.delete-btn {
  background-image: url(/demo-to-do-list/static/media/trash.862c8cbf.svg);
  width: 3em;
}
